@use "../vendors/sitefinity/framework";

.#{framework.$selector-prefix}-zone-toolset {
    display: inline-block;
    padding: 20px;
    border-radius: 3px;
    background-color: framework.$color-primary-invert;
    min-width: 280px;
    font-size: framework.$font-size-base;

    &__label {
        display: block;
        margin-bottom: 5px;
        text-transform: uppercase;
        color: framework.$color-primary;
        font-size: 10px;
    }

    input[type=number] {
        -moz-appearance: textfield;
    }
}
